<template>
	<div class="hospital">
		<div class="hospitals mb">
			<div class="hospital-item" :key="item.id" v-for="item in hospitalData" @click="changeMeal(item)">
				<div class="hospital-cover">
					<img :src="item.headImg">
				</div>
				<div class="hospital-body">
					<div class="hospital-meta-title">{{item.title}}</div>
					<div class="level">{{item.level}}</div>
					<p>{{item.detail}}</p>

					<div class="hospital-meta-tags">
						<div class="tag-logo"><img :src="item.logo"></div>
						<div class="address">{{item.address}}</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'hospital',
		data() {
			return {
				swiperData: [],
				swiperOption: {
					slidesPerView: 'auto',
					// spaceBetween: 10,
					pagination: {
						el: '.swiper-pagination',
						clickable: true
					}
				},
				hospitalSwiperData: [],
				hospitalData: [],
				publicPath: process.env.BASE_URL
			}
		},
		created() {
			fnCarousel(this);
			fnHospital(this);
			this.$emit('handlerTabbar', true);
		},
		methods: {
			changeCarousel(response) {
				if (response.type == 'index') {
					switch(response.index) {
						case 0:
							this.$router.push({ path: 'meal'});
							break;
						case 1:
							this.$router.push({ path: 'healthy'});
							break;
						case 2:
							this.$router.push({ path: 'example'});
							break;
					}
				}
			},
			changeMeal(item) {
				console.log(item);
				this.$router.push({ path: 'meal', query: { id: item.id } });
			}
		}
	}

	function fnCarousel(that, params) {
		that.$request.carousel(params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.swiperData = result.list.index;
			}
		})
	}

	function fnHospital(that, params) {
		that.$request.hospital(params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.hospitalData = result.list;
			}
		})
	}
</script>

<style lang="scss">
	@import './../styles/hospital.scss'
</style>